/* CreateDream.css */

.create-dream-page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background-color: #fafafa;
    align-items: center
}

.createDreamHeader {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    padding: 12px;
    background-color: white;
    border-bottom: 1px solid #dbdbdb;
}

.container {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90%;


}

.dream-input {
    margin-top: 50px;
    width: 100%;
    height: 120px;
    border: 1px solid #dbdbdb;
    border-radius: 4px;
    padding: 8px;
    font-size: 16px;
    resize: none;
}

.draw-button {
    width: 90%;
    height: 40px;
    margin-top: 8px;
    background-color: #0095f6;
    color: white;
    font-weight: bold;
    font-size: 14px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.dream-data {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #f0f0f0;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
    margin-bottom: 80px;
    width: 90%;
}

.dream-title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 8px;
}

.diary-list-item {
    display: flex;
    flex-direction: column;
    border: 1px solid #f0f0f0;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
    margin-bottom: 10px;
    width: 90%;
}

.dream-image {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 5px;
    margin-bottom: 10px;
}
.dream-image img {
    width: 100%;
    max-width: 614px; /* 이미지의 최대 폭 설정 */
    height: auto;
    object-fit: cover; /* 이미지를 자연스럽게 조정 */
}

.dream-content {
    margin-top: 8px;
    font-size: 16px;
    text-align: left;
}

.today-checklist ul {
    list-style-type: none;
    padding-left: 0;
}

.today-checklist li {
    display: flex;
    align-items: center;
}

.today-checklist li:not(:last-child) {
    margin-bottom: 8px;
}

.today-checklist input[type="checkbox"] {
    margin-right: 8px;
}
.dream-image {
    position: relative;
    display: inline-block;
}

